.sidebar {
  /* height: calc(100 * var(--vh)); */
  flex-grow: 1;
  background-color: rgb(241, 241, 241);
  display: flex;
  flex-direction: column;
  z-index: 10;

  border-right: 1px solid lightgray;
}

@media (min-width: 640px) {
  .sidebar {
    /* flex-grow: 0.35; */
