    max-width: 380px;
  }
}

.sidebar__header {
  display: flex;
  align-items: center;

  height: 50px;
}

.sidebar__avatar {
  cursor: pointer;
  margin: 10px;
}

.sidebar__search {
  display: flex;
  align-items: center;
  justify-content: left;
  padding-left: 5px;
  flex: 1 1;
  background-color: rgb(221, 221, 221);
  border-radius: 5px;
  color: gray;
}

.sidebar__search > input {
