  background: transparent;
  outline-width: 0;
}

.sidebar__chats {
  overflow: scroll;
}

.sidebarChat {
  display: flex;
  align-items: center;
  justify-content: space-around;
  font-size: 13px;
  height: 70px;

  border-bottom: 1px solid lightgrey;
}

.sidebarChat.sidebarChat--active {
